@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap");

.homeContainer {
  width: 100%;
}

.homeTitle {
  font-size: 32px;
  font-weight: 700;
  color: #07173d;
  margin: 0px 0px 20px 0px;
  text-align: center;
}
.homeTextMath {
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

.homeTextMath div {
  width: min-content;
}

.homeImageContainer {
  width: 100%;
  text-align: center;
  margin-bottom: 12px;
}
.infoElementContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 14px;
  margin-top: 14px;
  .infoElement {
    display: flex;
    flex-direction: column;
    padding: 16px;
    border-radius: 12px;
    border: 1px solid #0eb4ef;
    .infoElementTitle {
      font-size: 18px;
      font-weight: 600;
      color: #0f172a;
      margin-bottom: 8px;
    }
    .infoElementText {
      font-size: 16px;
      font-weight: 400;
      color: #0f172a;

      margin: 0;
    }
  }
}

.homeTextLink {
  color: #0eb4ef;
}
.homeTextLink:hover {
  transition: 0.1s;
  color: #0596cb;
}

.homeContainerBox {
  margin-top: 48px;
}

@media (max-width: 800px) {
  .homeTitle {
    font-size: 24px;
    margin: 0px;
  }
  .homeText {
    font-size: 14px;
  }
  img {
    width: 50%;
  }
  .infoElement {
    width: 90% !important;
  }
}
@media (max-width: 800px) {
  .homeTextMath {
    font-size: 12px;
  }
  .infoElementText {
    font-size: 14px !important;
  }
}
@media (max-width: 400px) {
  .homeTextMath {
    font-size: 11px;
  }
  .homeTitle {
    margin-bottom: 12px;
    font-size: 19px;
  }
  .infoElementTitle {
    font-size: 14px;
    margin: 0px;
  }
}
