.taskContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 14px;
  margin-top: 27px;
}
.taskElement {
  width: 90%;
  display: flex;
  flex-direction: column;
  padding: 16px;
  border-radius: 12px;
  border: 2px solid #09bdc6;
}
.taskTitle {
  font-size: 28px;
  font-weight: 600;
  color: #000;
  margin-top: 0px;
  margin-bottom: 6px;
}
.taskText {
  font-size: 16px;
  font-weight: 400;
  color: #000;
}

@media (max-width: 1200px) {
  .taskElement {
    width: auto;
  }
}

@media (max-width: 800px) {
  .taskElement {
    width: 90%;
    padding: 10px;
  }
  .taskTitle {
    font-size: 21px;
  }
  .taskText {
    font-size: 15px;
  }
}
@media (max-width: 500px) {
  .taskElement {
    width: 90%;
    padding: 10px;
  }
  .taskTitle {
    font-size: 18px;
  }
  .taskText {
    font-size: 11px;
  }
}
@media (max-width: 350px) {
  .taskTitle {
    font-size: 14px;
  }
}
