body {
  height: 100vh;
  flex-direction: column;
}

.footer {
  margin-top: 24px;
  padding: 24px;
  background-color: #1e1d20 !important;
  .footerColumns {
    display: flex;
    justify-content: space-between;
    .footerColumn {
      display: flex;
      flex-direction: column;
    }
  }

  .footerLogo {
    background: #121fcf;
    background: linear-gradient(90deg, #338cf5 0%, #2fc6cd 50%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 800;
    font-size: 25px;
    cursor: pointer;
  }
}
.footerColumLeft {
  width: 20%;
}

.footerColumnTitle {
  color: #fff;
  font-weight: 700;
  font-size: 24px;
  margin-bottom: 8px;
}
.footerColumnText {
  color: #fff;
  font-weight: 700;
  font-size: 28px;
  margin-bottom: 18px;
}
.footerLink {
  font-weight: 400;
  font-size: 15px;
  text-transform: none;
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  transition: color 0.2s ease 0s, text-decoration 0.2s ease 0s;
  cursor: pointer;
  margin-bottom: 4px;
}

.footerText {
  font-weight: 400;
  font-size: 15px;
  text-transform: none;
  color: rgba(255, 255, 255, 0.7);
}

.footerLink:hover {
  color: rgba(255, 255, 255, 0.4);
}

@media (max-width: 1000px) {
  .footerColumLeft {
    width: 55%;
    .footerColumnTitle {
      font-size: 12px !important;
    }
    .footerLogo {
      font-size: 18px;
    }
  }
  .footerText {
    font-size: 12px;
  }
}
@media (max-width: 600px) {
  .footerColumLeft {
    width: 75%;
  }
  .footerText {
    font-size: 12px;
  }
  .footerColumnTitle {
    font-size: 16px !important;
  }
  .footerLogo {
    font-size: 18px;
  }
}
@media (max-width: 450px) {
  .footerColumLeft {
    width: 50%;
  }
  .footerText {
    font-size: 10px;
  }
  .footerColumnTitle {
    font-size: 16px !important;
  }
  .footerLogo {
    font-size: 18px;
  }
}
